@import "master";

@media (min-width: 993px) {
  .mbp-lg-50 {
    margin-bottom: 50px !important;
  }

  .mrp-lg-30 {
    margin-right: 30px !important;
  }

  .product-card {
    &-wrap {
      &:hover {
        padding: 20px;
        margin-top: -20px;

        position: absolute;
        z-index: 15;
        box-shadow: 0px 20px 50px rgba(0, 0, 0, 0.15);
        // width: calc(100% + 20px);
        // margin-left: -10px;
        // padding-left: 25px;
        // padding-right: 25px;
        .product-card__attributes {
          display: block;
          animation: productAttributesAnimation 500ms;
        }
        .product-card__actions-panel {
          display: block;
        }
      }
    }
  }
}

