@media (max-width: 1200px) {
  .products-grid {
    &__item {
      width: 33%;
    }
  }
}

@media (max-width: 992px) {
  // responsiveness limits
  html,
  body {
    min-width: 0;
  }
  .main-header,
  .main-container,
  .main-footer {
    min-width: 0;
  }

  .main-container {
    &__content {
      &-wrap {
        margin-left: -30px;
        margin-right: -30px;
      }
    }
  }

  .product-media-gallery {
    margin-left: -30px;
    margin-right: -30px;

    &__top {
      border-radius: 0 0 30px 30px;
    }
    &__thumbs {
      &-wrap {
        display: none;
      }
    }
  }

  body {
    background-color: #fff;
    background-image: none;
  }

  .main-header {
    &__top {
      .language-switcher {
        display: none !important;
      }
      &-content {
        &-center {
          width: auto;
        }
        &-right {
          width: 100%;
        }
      }
    }
    &__bottom {
      display: none;
    }

    &__small-bar {
      margin-bottom: 0;
    }
  }

  .main-footer {
    &__nav-menu {
      display: none;
    }
  }

  .main-nav-panel {
    &-wrap {
      position: fixed;
      left: 0;
      bottom: 0;
      width: 100%;
      z-index: 10;
      background-color: $main;
      padding: 10px 0;
    }
    justify-content: center;

    &__item {
      font-size: 12px;
      &:not(:last-child) {
        margin-right: 60px;
      }

      a {
        & > * {
          display: block;
        }
      }
    }
  }

  .applied-filters-panel {
    &__filters {
      display: none;
    }
    &__sort {
      span {
        display: none;
      }
    }
  }

  .applied-filters-panel {
    border-bottom: 1px solid $black-lines;
    padding-bottom: 10px;
    margin-bottom: 20px;
    &__sort {
      justify-content: flex-start;
    }
  }
  .products-grid {
    // overflow: hidden;

    &__item {
      width: 50%;
      min-height: 0;
    }

    &.products-grid--c-5 {
      .products-grid__item {
        flex: 0 0 auto;
        width: 240px;
      }
    }
  }

  .product-card {
    &:not(.product-card--sm-big) {
      .product-card-wrap {
        padding-right: 0;
      }
      display: flex;
      padding: 0;

      & > div:nth-child(1) {
        width: 50%;
        margin-right: 20px;
        position: relative;
        display: table;
      }
      & > div:nth-child(2) {
        width: 50%;
      }

      .product-card__title {
        font-size: 1rem;
        line-height: 21px;
        font-weight: bold;
        margin-bottom: 0;
      }
      .product-card__label {
        top: auto;
        bottom: 6px;
        left: 50%;
        transform: translateX(-50%);
        width: 80%;
        text-align: center;
      }
      .product-card__size {
        display: block;
      }
      .product-card__image {
        border: 1px solid $black-lines;
        border-radius: 20px;
      }
      .product-card__rate {
        margin-bottom: 0;
      }
      .product-card__footer {
        .btn {
          display: none;
        }
      }
    }
  }

  .product-page {
    &__description {
      width: 100%;
    }
    &__actions-panel {
      justify-content: space-between;
      &-control-btn {
        span {
          display: none;
        }

        &.product-page__compare-btn {
          display: none;
        }
      }
    }

    &__short-description {
      display: none;
    }

    &__attributes {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      &-item {
        width: 50%;
        margin-bottom: 20px;
      }
    }

    &__reviews {
      flex-direction: column;

      &-summary {
        width: 100%;
        padding-right: 0;

        &-total {
          i {
            font-size: 30px;
            margin-right: 5px;
          }
          &-value {
            font-weight: bold;
            font-size: 18px;
            line-height: 27px;
            margin-left: 20px;
          }
        }

        &-score {
          display: none;
        }
      }
      &-comments {
        width: 100%;
        padding-left: 0;
      }
      &-comment {
        &:not(:last-child) {
          margin-bottom: 30px;
        }
        &-meta {
          display: flex;
          align-items: center;
          margin-bottom: 10px;
          &-rate {
            white-space: nowrap;
            margin-right: 5px;
            i {
              font-size: 10px;
            }
          }
        }
        &-uploads {
          display: flex;
          &.slick-initialized {
            margin-right: -30px;
          }
        }
      }
    }
  }
  .product-media-gallery {
    border-radius: 0 0 30px 30px;
    margin-bottom: 20px;
    &__label {
      top: 20px;
    }
    &__top {
      .swiper-control {
        display: flex;
      }
      border-radius: 0 0 30px 30px;
      .swiper-slide {
        border-radius: 0 0 30px 30px;
      }
    }
    .swiper-container {
      height: 300px;
    }
  }
  .product-spec {
    &:last-child {
      margin-bottom: 10px;
    }
  }

  .product-page-sidebar {
    padding: 0;
    border-radius: 0;
    background: none;
    & > :nth-child(1),
    & > :nth-child(2),
    & > :nth-child(3) {
      display: none;
    }
  }

  .product-posts-grid {
    &-wrap {
      width: 100%;
    }

    &__item {
      &-wrap {
        width: 100%;

        &:first-child {
          width: 100%;
          .product-posts-grid__item {
            flex-direction: column;
          }
          .product-posts-grid__item-thumbnail-wrap {
            width: 100%;
            margin-bottom: 20px;
          }
          .product-posts-grid__item-content {
            padding-left: 0;
          }
        }
      }
    }
  }

  .cart-table {
    &__item {
      display: grid;
      column-gap: 20px;
      row-gap: 0;
      grid-template-columns: 50% 50%;
      grid-template-rows: 1fr auto;
      grid-template-areas:
        "image main"
        "image price"
        "image quantity"
        "actions total";

      &-price {
        margin-top: 10px;
      }
      &-quantity {
        margin-top: 10px;
      }
      &-total {
        margin-top: 10px;
      }

      &-weight {
        display: none;
      }
      &-divider {
        display: none;
      }

      &-actions-panel {
        display: flex;

        &-rate {
          white-space: nowrap;
          margin-right: 15px;
          i {
            font-size: 10px;
          }
        }

        &-controls {
          margin-top: 20px;
          display: flex;
          flex-direction: column;
          align-items: flex-start;

          .cart-table__item-compare-btn {
            display: none;
          }
        }
        &-control-btn {
          font-size: 0.8571rem;
          line-height: 17.9998px;
          text-decoration: none !important;

          &:not(:last-child) {
            margin-right: 0;
            margin-bottom: 10px;
          }
        }
      }
    }

    &__quantity-control {
      padding: 5px 0;
    }
  }

  .cart-page-sidebar {
    &__top {
      margin-bottom: 20px;
    }
    &__bottom {
      margin-bottom: 40px;
    }
    &__header {
      display: none !important;
    }
    &__control-panel {
      display: none;
    }
  }

  .stepper-header {
    margin-bottom: 10px;
  }

  .cart-table-header {
    grid-template-columns: 50% 50%;
    grid-column-gap: 0;
    margin-bottom: 20px;

    &__item-weight {
      display: none;
    }
    &__item-price {
      display: none;
    }
    &__item-quantity {
      display: none;
    }
    &__item-total {
      display: none;
    }
    &__item-total-weight {
      display: block;
      text-align: right;
    }
  }

  .cart-page-floating-bar {
    &-wrap {
      display: block;
      position: fixed;
      width: 100%;
      bottom: 60px;
      left: 0;
      z-index: 10;
    }
  }

  .blog-posts-grid {
    // overflow: hidden;
    &__item {
      &-wrap {
        width: 240px;
        flex: 0 0 auto;
      }
      &-video-btn {
        display: block;
      }
    }
  }

  .promo-banners {
    &-wrap {
      margin-top: -40px;
      overflow: hidden;
      margin-left: -30px;
      margin-right: -30px;
    }

    display: flex;
    flex-wrap: wrap;
    margin-left: -10px;
    margin-right: -10px;
    // margin-left: -30px;
    // margin-right: -30px;

    &__item {
      padding: 0 20px;

      &:nth-child(1) {
        margin-left: 0;
        border-radius: 0 0 20px 20px;

        .promo-banners__item-image {
          height: 220px;
          border-radius: 0 0 20px 20px;
        }
        .promo-banners__item-image-wrap {
          &::after {
            border-radius: 0 0 20px 20px;
          }
        }
      }
      &:nth-child(2) {
        margin-top: 0;
        border-radius: 0 0 20px 20px;

        .promo-banners__item-image {
          height: 220px;
          border-radius: 0 0 20px 20px;
        }
        .promo-banners__item-image-wrap {
          &::after {
            border-radius: 0 0 20px 20px;
          }
        }
      }
      &:nth-child(3) {
        border-radius: 0 0 20px 20px;

        .promo-banners__item-image {
          height: 220px;
          border-radius: 0 0 20px 20px;
        }
        .promo-banners__item-image-wrap {
          &::after {
            border-radius: 0 0 20px 20px;
          }
        }
      }

      &-image {
        &-wrap {
          margin-left: -10px;
          margin-right: -10px;
        }
      }
    }
  }

  .main-nav-cards-grid {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 0;
    margin-bottom: 40px;

    &__item {
      &-wrap {
        width: 25%;
        padding: 0;
        margin: 0;

        a {
          text-decoration: none;
        }
      }

      align-items: center;
      padding: 0;
      box-shadow: none;

      &:hover {
        background: transparent;
        &::after {
          display: none;
          content: none;
        }
        .main-nav-cards-grid__item-title {
          color: $action;
        }
      }

      &-image {
        &-wrap {
          position: relative;
          border-radius: 50%;
          width: 65px;
          height: 65px;
          overflow: hidden;
          background: $main;
          &::after {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            border-radius: 20px;
            background: linear-gradient(
              293.85deg,
              #ff35ba -17.55%,
              rgba(190, 53, 255, 0) 62.96%
            );
          }
        }

        position: absolute;
        left: 0;
        top: 0;
        z-index: 1;
      }

      &-title {
        // display: none;
        font-weight: normal;
        font-size: 12px;
        line-height: 18px;
        color: $action;
        margin: 0;
        margin-top: 5px;
      }
      &-content {
        display: none;
      }
      &-footer {
        display: none;
      }
    }
  }
  .sub-nav-cards-grid {
    display: flex;
    flex-wrap: wrap;
    margin-left: -10px;
    margin-right: -10px;
    // overflow: hidden;

    &__item {
      &-wrap {
        width: 240px;
        flex: 0 0 auto;
      }
    }
  }

  .overlay-cdk {
    background: $white;
    padding: 0;
    &__close-btn {
      display: flex;
      font-size: 15px;
      background: $white;
      color: $action;
      padding: 10px;
      border-radius: 50%;
    }
    &__content {
      margin-left: -10px;
      margin-right: -10px;

      &-video {
        &-wrap {
          height: 38%;
        }
        border-top-left-radius: 0;
        border-top-right-radius: 0;
      }
    }
  }

  .mobile-slider {
    &.slick-initialized {
      margin-left: -30px;
      margin-right: -30px;

      .slick-list {
        padding-right: 20px;
        padding-left: 20px;
      }

      // .slick-slide {
      //   // margin-left: 20px;
      //   // margin-right: 20px;
      //   // &:last-child {
      //   //   margin-left: -20px;
      //   //   margin-right: 0;
      //   // }
      //   // &:first-child {
      //   //   margin-left: 20px;
      //   // }
      //   // &:last-child {
      //   //   margin-right: 20px;
      //   // }
      // }
    }
  }

  .static-seo {
    &__text {
      max-height: 200px;
      overflow: hidden;
      position: relative;
      margin-bottom: 10px;
      &::after {
        position: absolute;
        content: "";
        bottom: 0;
        left: 0;
        width: 100%;
        background: linear-gradient(
          180deg,
          rgba(255, 255, 255, 0) 0%,
          #ffffff 100%
        );
        height: 100px;
      }
    }

    &.active {
      .static-seo__text {
        max-height: none;
        overflow: initial;
        &::after {
          display: none;
        }
      }
    }
  }
}
@media (max-width: 768px) {
  .main-container {
    &__content {
      padding: 0;
      padding-top: 40px;
      border-radius: 0;
      margin-left: 0;
      margin-right: 0;
    }
  }

  .main-footer {
    .container {
      padding-left: 30px;
      padding-right: 30px;
    }
  }
}
@media (max-width: 576px) {
  .main-header {
    &__logo-wrap {
      width: 130px;
      flex: 0 0 auto;
    }
    &__top {
      &-content {
        &-center {
          margin: 0;
        }
      }
    }
  }

  .main-container {
    .container {
      padding-left: 30px;
      padding-right: 30px;
    }
  }

  .banner-card {
    height: auto;
    &--1 {
      background-position-x: 70%;
    }

    &__title {
      max-width: none;
    }
    &__content {
      max-width: none;
      margin-bottom: 50px;
    }
  }

  .products-grid {
    &__item {
      width: 100%;
    }
  }
}
@media (max-width: 365px) {
  .main-header {
    &__top {
      &-content {
        &-center {
          display: none;
        }
      }
    }
  }

  .main-nav-cards-grid {
    &__item {
      &-image {
        &-wrap {
          width: 60px;
          height: 60px;
        }
      }
    }
  }
}
